'use strict';

export default class View {
    /**
     *   view
     * @param {Object} [options={}] -   
     */
    constructor(options = {}) {
        this.tagName = options.tagName || 'div';
        this._el = document.createElement(this.tagName);
    }

    /**
     *   view (   )
     *  
     * @param {Object} [options={}] -   
     */
    init(options = {}) {
        this.setAttrs(options.attrs);
        this.setClasses(options.classes);
    }

    /**
     *     view (  view     view)
     *    
     * @param {Object} [options={}] -   
     */
    pause(options = {}) {
        this.hide();
    }

    /**
     *       view ( ,  view  )
     *    
     * @param {Object} [options={}] -   
     */
    resume(options = {}) {
        this.show();
    }

    /**
     *  view
     * @param {Object} [options={}] -   
     */
    show(options = {}) {
        this._el.style.display = 'flex';
    }

    /**
     *  view
     * @param {Object} [options={}] -   
     */
    hide(options = {}) {
        this._el.style.display = 'none';
		this._el.hidden = true;
    }

    /**
     *  view
     *  
     * @param {Object} [options={}] -   
     */
    render(options = {}) {

    }

    /**
     *   view   
     * @param {HTMLElement} el - HTML-,      view
     */
    appendTo(el) {
        el.appendChild(this._el);
    }

    /**
     *    view
     */
    remove() {
        this._el && this._el.remove();
    }

    /**
     *    view
     * @param {HTMLElement} el - HTML-,     view
     */
    setElement(el) {
        this._el && this._el.remove();
        this._el = el;
    }

    /**
     *   view  
     * @param {Object} [attrs={}] -   ,       view
     */
    setAttrs(attrs = {}) {
        Object.keys(attrs).forEach(name => {
            this._el.setAttribute(name, attrs[name]);
        })
    }

    setClasses(classes = []) {
        classes.forEach((name) => {
            this._el.classList.add(name);
        });
    }

    /**
     *  ,     view
     * @returns {string}
     */
    toString() {
        return this._el.outerHTML;
    }

    /**
     *   view 
     * @param {Router} router -  
     */
    setRouter(router) {
        this.router = router;
    }
}
window.View = View;



