'use strict';

import  pathToRegex  from "./pathToRegex"
let id = 0;

/**        */
export default class Route {
    /**
     *   Route -   view   
     * @param {string} pathname -  
     * @param {View} view -   View
     * @param {Object} [options={}] -  ,     view     
     */
    constructor(pathname, view, options = {}) {
        //TODO:  ,    
        this.pathToRegex = window.pathToRegex;
        this.id = 'p' + id;
        id++;
        this.pathname = pathname;
        this.regex = this.pathToRegex(pathname);
        this.View = view;
        this.options = options;
    }

    /**
     * ,    pathname  Route
     * @param {string} pathname -   
     * @returns {boolean}  
     */
    match(pathname) {
        return !!this.regex(pathname);
    }

    /**
     *   Route (  )
     * @param {string} pathname -   
     * @param {Object} [state={}] -  state,      popstate   window
     */
    navigate(pathname, state = {}) {
        state = state || {};
        let keys = this.regex(pathname);
        if (!this._view) {
            let view = new this.View(this.options);
            view.init(this.options);
            view.setRouter(this.__router);
            this._view = view;
        }

        this._view.resume(Object.assign(state, keys));
    }

    /**
     *   Route
     */
    leave() {
        this._view && this._view.pause();
    }

    /**
     *   Route  
     * @param {Router} router -  
     */
    setRouter(router) {
        this.__router = router;
    }
}
window.Route = Route;




